import React from 'react'
import {AccList} from '../Comp/Aarray'
import ProCard from '../Comp/ProCard'
import { BrowserRouter as Router, Routes, Route, Link } from "react-router-dom";
import Navbar from '../Comp/Navbar';


const Acces = () => {
  return (
    <>
<Navbar/>
    
    <div className='product'>
      <h1 className="protittle"> Accesseries </h1>
      <div className="prodiv">
      {AccList.map((item)=>(
          //  <ProCard src={item.src} key={item.src} />
          <div className="procard">
    <img src={item.src} alt="" className="proimg" />
    <div className="procardbottom">
    <Link to={item.path}> <button>Buy Now</button></Link>
        <h4>6$</h4>
    </div>
</div>
        ))}
      </div>
    </div>
    </>
  )
}

export default Acces;
