import React,{useState} from 'react'
import {BrowserRouter as Router,Switch,Route,Link} from 'react-router-dom'
const Navbar = () => {

  const [xyz,setXyz]=useState("initial")
  const myStyle={
    display:xyz}
  const click=()=>{
if(xyz=="initial"){
  console.log('if')
  setXyz("none")
}else{
  console.log('else')
  setXyz("initial")
}
  }
  return (
    <>
    <div className='navbar'>
    <span class="material-symbols-outlined" onClick={click}>
menu
</span>
      <div className="navl">
     
        <Link to="/"><h3 style={myStyle}>Home</h3></Link>
        <Link to="/men"><h3 style={myStyle}>Men</h3></Link>
        <Link to="/women"><h3 style={myStyle}>Women</h3></Link>
        <Link to="/acces"><h3 style={myStyle}>Acceceseries</h3></Link>
        {/* <Link to="/tech"><h3>Tech</h3></Link> */}
      </div>
      <div className="navm"> Shopify </div>
      <div className="navr">
        <h3>Register</h3>
        <h3>Sign In</h3>
        
      </div>
    </div>
<div className="tapmenu">
       <div><Link to="/men"><h3>Men</h3></Link></div>
       <div><Link to="/women"><h3>Women</h3></Link></div>
       <div><Link to="/acces"><h3>Acceceseries</h3></Link></div>
</div>
    </>
  )
}

export default Navbar
