import React from 'react';
import {Sale} from './Aarray'
import {Link } from "react-router-dom";



const Slider = () => {


  return (
    <div className='slider' >
      <span className="material-symbols-outlined naval"  >
chevron_left
</span>
<div className='slide-wrapper'>
  <div className="slidecont" 
  // style={mystyle}
  >
{Sale.map((item)=>(
        <div className="slide">
        <div className="img-cont">
            <img className='slideimg' src={item.img} key={item.src} alt="" />
        </div>
        <div className="info-cont" style={item.bgc}>
            <h3>{item.sale}</h3>
            <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Laboriosam, iure?</p>
            <Link to={item.path}> <button>Shop Now</button></Link>
        </div>
    </div>
      ))}</div>
   </div>
<span className="material-symbols-outlined navar"  >
chevron_right
</span></div>
  )
}
 
export default Slider
