
import React from 'react'
import CatItem from './CatItem'
import {Category} from './Aarray'
const Cat = () => {
  return (
    <>
    <h2 class="cathead">Explore from the best</h2>
    <div className='cat'>
      
      <div className="catdiv">
        {Category.map((item)=>(
            <CatItem key={item.tittle} image={item.img} tittle={item.tittle} path={item.path}/>
        ))}
      </div>
    </div>
    </>
  )
}

export default Cat
